.addToCart {
    font-size: 1rem;
    font-weight: 500;
    text-align: center;
    width: 7rem;
    padding: 0.5rem 2rem 0.6rem 2rem;
    border-radius: 0.2rem;
    cursor: pointer;
    border: 1px solid;
}

@media (max-width: 415px) {
    .addToCart {
        font-size: 1.5rem;
        width: max-content;
    }
}
