.multi-range-slider * {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}
.multi-range-slider {
    display: flex;
    position: relative;
    border: solid 1px transparent;
    border-radius: 10px;
    box-shadow: none;
    width: 8rem;
    margin: auto;
    padding: 20px 2rem 15px 2px;
    flex-direction: column;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge,*/
}

.multi-range-slider .bar {
    display: flex;
}
.multi-range-slider .bar-left {
    width: 25%;
    background-color: #ffffff;
    border-radius: 0px;
    box-shadow: inset 0px 0px 5px rgb(92, 92, 92);
    padding: 1px 0px;
}
.multi-range-slider .bar-right {
    width: 25%;
    background-color: #ffffff;
    border-radius: 0px;
    box-shadow: inset 0px 0px 5px rgb(92, 92, 92);
}
.multi-range-slider .bar-inner {
    background-color: #000000;
    display: flex;
    flex-grow: 1;
    flex-shrink: 1;
    justify-content: space-between;
    position: relative;
    border: none;
    justify-content: space-between;
    box-shadow: inset 0px 0px 1px black;
}
.multi-range-slider .bar-inner-left {
    width: 50%;
}
.multi-range-slider .bar-inner-right {
    width: 50%;
}
.multi-range-slider .thumb {
    background-color: white;
    position: relative;
    z-index: 1;
    cursor: pointer;
}
.multi-range-slider .thumb::before {
    content: '';
    background-color: #212121;
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    border: solid 1px transparent;
    box-shadow: 1px 1px 2px #a6a6a6;
    border-radius: 50%;
    z-index: 1;
    margin: -0.7rem 0rem;
    cursor: pointer;
}
.multi-range-slider .input-type-range:focus + .thumb::after {
    content: '';
    position: absolute;
    top: -4px;
    left: -7px;
    width: 11px;
    height: 11px;
    z-index: 2;
    border-radius: 50%;
    border: dotted 1px black;
    box-shadow: 0px 0px 5px white, inset 0px 0px 10px black;
}
.multi-range-slider .caption {
    position: absolute;
    bottom: 35px;
    width: 2px;
    height: 2px;
    left: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
    display: none;
}
.multi-range-slider .thumb .caption * {
    position: absolute;
    min-width: max-content;
    height: 30px;
    font-size: 75%;
    text-align: center;
    line-height: 30px;
    background-color: #000000;
    border-radius: 0.5rem;
    color: rgb(218, 218, 218);
    box-shadow: 0px 0px 5px black;
    padding: 0rem 0.5rem;
    white-space: nowrap;
}
.multi-range-slider .thumb:active .caption {
    display: flex;
}
.multi-range-slider .input-type-range:focus + .thumb .caption {
    display: flex;
}
.multi-range-slider .input-type-range {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    opacity: 0;
    pointer-events: none;
}

.multi-range-slider .ruler {
    margin: 10px 0px -5px 0px;
    display: flex;
    /* display: none; */
    overflow: hidden;
}
.multi-range-slider .ruler .ruler-rule {
    border-left: solid 1px;
    border-bottom: solid 1px;
    display: flex;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 5px 0px;
}
.multi-range-slider .ruler .ruler-rule:last-child {
    border-right: solid 1px;
}

.multi-range-slider .ruler .ruler-sub-rule {
    border-left: solid 1px;
    /* border-bottom: solid 1px; */
    display: flex;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 3px 0px;
    bottom: 0px;
    margin-bottom: -5px;
}
.multi-range-slider .ruler .ruler-sub-rule:first-child {
    border-left: none;
}

.multi-range-slider .labels {
    /* border: 1px red solid; */
    display: flex;
    justify-content: space-between;
    padding: 0px;
    margin-top: 15px;
    margin-bottom: -20px;
    /* display: none; */
}
.multi-range-slider .label {
    /* border: 1px red solid; */
    font-size: 80%;
    display: flex;
    width: fit-content;
    justify-content: center;
}
.multi-range-slider .label:first-child {
    justify-content: start;
    margin-left: -1.5rem;
}
.multi-range-slider .label:last-child {
    justify-content: end;
    margin-right: -1.5rem;
}
.multi-range-slider.zero-ranage-margin .thumb-left {
    right: 12px;
}
.multi-range-slider.zero-ranage-margin .thumb-right {
    left: 8px;
}
